import React, { useEffect } from "react";
import Header from "./component/Header";
import Content from "./component/Content";
import { AppViewStatus } from "./component/ViewStatus";
import MenuSelecterMain from "./component/MenuSelecterMain";
import Cart from "./component/Cart";
import { CustomOptions } from "./component/Orderdata";

var flag = true;

function App() {
  // use it in the function, get all the students
  const { value } = AppViewStatus();
  const { valueoflist, setvalueoflist } = CustomOptions();
  function setScreenSize() {
    let vh = window.innerHeight * 0.01;
    let vw = window.innerWidth * 0.01;
    document.documentElement.style.setProperty("--vh", `${vh}px`);
    document.documentElement.style.setProperty("--vw", `${vw}px`);
  }
  React.useEffect(() => {
    setScreenSize();
  });

  useEffect(() => {
    document.body.style.cssText = `
        position: fixed; 
        top: -${window.scrollY}px;
        overflow-y: scroll;
        width: 100%;`;
    return () => {
      const scrollY = document.body.style.top;
      document.body.style.cssText = "";
      window.scrollTo(0, parseInt(scrollY || "0", 10) * -1);
    };
  }, []);
  if (flag) {
    var vl = [];
    for (var k = 0; k < 51; k++) {
      // .
      vl.push({
        id: k,
        ice: "0", // 0 1 2 3
        water: "0", //0 1 2
        sweet: "0", //0 1 2
        shotamount: "0", //0  1,(500) 2
        pulp: "0", //0,1
        soymilk: "0", // 0() 1
        whippedcream: "0", //:  0 1 500
        excludesyrup: "0", //0 1
        excludetopping: "0", // 0 1
        includedalgona: "0", // 0 1(500)
        addhazelnutsyrup: "0", // 0 1(500)
        addcinnamonsyrup: "0", // 0 1(500)
        changenormalintosignature: "0", // 0 1(200)
        changebanaintosignature: "0", // 0 1(400)
        changelatteorsoyfromset: "0", // 0  1 2(800)
      });
    }
    /////////////////////////////////////
    document.documentElement.addEventListener(
      "touchstart",
      function (event) {
        //
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      },
      false
    );

    var lastTouchEnd = 0;

    document.documentElement.addEventListener(
      "touchend",
      function (event) {
        //
        var now = new Date().getTime();
        if (now - lastTouchEnd <= 300) {
          event.preventDefault();
        }
        lastTouchEnd = now;
      },
      false
    );

    ///////////////////////////////////////////

    setvalueoflist(vl);
    var vl = [];
    console.log(valueoflist);
    flag = false;
    console.log(flag);
  }
  return (
    <div>
      {value === 1 ? (
        <div
          className="container"
          style={{ display: "flex", flexDirection: "column" }}
        >
          {console.log(valueoflist)}
          <Header className="item" style={{}} />
          <Content
            className="item"
            style={{ flexDirection: "row", justifyContent: "center" }}
          />
        </div>
      ) : (
        <p></p>
      )}

      {value === 2 ? (
        <div
          className="container"
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <Header
            className="item"
            style={{ flexDirection: "row", justifyContent: "center" }}
          />
          <MenuSelecterMain
            className="item"
            style={{ flexDirection: "row", justifyContent: "center" }}
          />
          <Cart
            className="item"
            style={{ flexDirection: "row", justifyContent: "center" }}
          />
        </div>
      ) : (
        <p></p>
      )}
      {value === 3 ? (
        <div
          className="container"
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <Header
            className="item"
            style={{ flexDirection: "row", justifyContent: "center" }}
          />
          <MenuSelecterMain
            className="item"
            style={{ flexDirection: "row", justifyContent: "center" }}
          />
          <Cart
            className="item"
            style={{ flexDirection: "row", justifyContent: "center" }}
          />
        </div>
      ) : (
        <p></p>
      )}
    </div>
  );
}

export default App;
